.answer-list {
	display: flex;
	flex-direction: column;
	align-items: center;
	background-color: rgba(240,248,255, 0.9);
	width: 70%; 
	margin-left: 15%;
	padding: 1em;
	border-radius: 2%;
}	

.answer-list-item {
	border: 1px solid black;
	cursor: pointer;
	text-align: left;
	width: 100%;
}

.answer-list-item:hover {
	background-color: #DCDCDC;
}

.answer-list-item-select {
	background-color: #DCDCDC;
}

.score p {
	font-size: 2em;
	font-weight: bold;
}

.spinner {
	width: 3rem;
	height: 3rem;
}

img {
	height: 250px;
	margin-bottom: 10px;
}