.app {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	width: 100%;
	font-family: 'Reilway', sans-serif;
}

.content {
	display: flex;
	flex-direction: column;
	justify-content: center;
	background-image: url(../../img/shipcrash.jpg);
	width: 100%;
	height: 900px;
	background-size: auto 100%;
	padding: 10px;
	color: #FF0000;
	
}

.content h1 {
	font-weight: bold;
	font-size: 4em;
}

@media (max-width: 600px) and (min-width: 320px) {
	.content {
		background-size: cover;
	}
}
